import { API_CONFIG } from '@/config';

/**
 * API ÊúçÂãôÂü∫Â∫ïÈ°ûÂà•
 * Êèê‰æõÈÄöÁî®ÁöÑ API Ë´ãÊ±ÇËôïÁêÜÈÇèËºØ
 */
export abstract class BaseApiService {
    protected readonly serviceName: string;

    constructor(serviceName: string) {
        this.serviceName = serviceName;
    }

    /**
     * ÈÄöÁî®ÁöÑ API Ë´ãÊ±ÇÊñπÊ≥ï
     * @param endpoint Á´ØÈªûÈÖçÁΩÆ { mock: string, external: string }
     * @param options È°çÂ§ñÁöÑ fetch ÈÅ∏È†Ö
     * @returns Promise<T>
     */
    protected async apiRequest<T>(
        endpoint: { mock: string; external: string },
        options: RequestInit = {}
    ): Promise<T> {
        const url = this.buildApiUrl(endpoint);

        try {
            this.logApiCall(url);

            const response = await fetch(url, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    ...options.headers,
                },
                // Âè™ÊúâÊ≠£Âºè API ÈúÄË¶ÅË∂ÖÊôÇË®≠ÂÆö
                ...(API_CONFIG.USE_MOCK ? {} : {
                    signal: AbortSignal.timeout(API_CONFIG.TIMEOUT.DEFAULT)
                }),
                ...options,
            });

            if (!response.ok) {
                throw new Error(`${this.serviceName} API Ë´ãÊ±ÇÂ§±Êïó: ${response.status} ${response.statusText}`);
            }

            const apiResponse = await response.json();

            // ËôïÁêÜ API ÂõûÊáâÊ†ºÂºè { code, message, data }
            const data = apiResponse.data || apiResponse; // ÂÖºÂÆπ‰∏çÂêåÁöÑÂõûÊáâÊ†ºÂºè

            this.logSuccess(data);

            return data;
        } catch (error) {
            this.logError(error);
            throw error;
        }
    }

    /**
     * Ê†πÊìöÁí∞Â¢ÉËÆäÊï∏Âª∫Êßã API Á∂≤ÂùÄ
     */
    private buildApiUrl(endpoint: { mock: string; external: string }): string {
        if (API_CONFIG.USE_MOCK) {
            // Mock API - ‰ΩøÁî®ÂÖßÈÉ® Next.js API Routes
            return `${API_CONFIG.BASE_URL}${endpoint.mock}`;
        } else {
            // Ê≠£Âºè API - ‰ΩøÁî®Â§ñÈÉ®ÂæåÁ´Ø API
            return `${API_CONFIG.EXTERNAL_BASE_URL}${endpoint.external}`;
        }
    }

    /**
     * Ë®òÈåÑ API ÂëºÂè´Êó•Ë™å
     */
    protected logApiCall(url: string): void {
        console.log(`üåê ${this.serviceName} API ÂëºÂè´: ${url} (Mock: ${API_CONFIG.USE_MOCK})`);
    }

    /**
     * Ë®òÈåÑÊàêÂäüÊó•Ë™å - Â≠êÈ°ûÂà•ÂèØ‰ª•Ë¶ÜÂØ´Ëá™ÂÆöÁæ©Ê†ºÂºè
     */
    protected logSuccess(data: any): void {
        if (API_CONFIG.LOGGING) {
            console.log(`‚úÖ ${this.serviceName}Ë≥áÊñôËºâÂÖ•ÊàêÂäü`);
        }
    }

    /**
     * Ë®òÈåÑÈåØË™§Êó•Ë™å
     */
    protected logError(error: any): void {
        console.error(`‚ùå ${this.serviceName} API ÂëºÂè´Â§±Êïó:`, error);
    }
}